.container {
  background-color: rgb(139, 133, 157);
  display: flex;
  
  align-items: center;
  flex-direction: column;
  padding: 30px;
  
}

.heading {
  color: rgb(89, 4, 4);
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;

  font-size: large;
}

.display-password {
  padding: 7px;
  border-radius: 8px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgb(23, 18, 18);
  font-size: medium;
  background-color: rgb(198, 255, 236);
}

.copy-btn {
  color: rgb(224, 230, 230);
  background-color: rgb(31, 41, 244);
  padding: 6px;
  border-radius: 4px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.cursor-pointer {
  cursor: pointer;
}

.charcheck,
.numcheck {
  color: rgb(16, 19, 7);
  font-size: medium;
}

.charcheckbox,
.numcheckbox {
  margin-right: 8px;
}
